import { useMermaid } from '@/hooks/useMermaid.ts';
import prism from '@/styles/prism.ts';
import { PostFullContent } from '@/styles/shared.ts';

function PostContent({ html }: { html: string }) {
  const { contentRef } = useMermaid(html);

  return (
    <PostFullContent className="post-full-content" css={prism}>
      <div ref={contentRef} className="post-content" dangerouslySetInnerHTML={{ __html: html }} />
    </PostFullContent>
  );
}

export default PostContent;
