import { useCallback, useContext, useEffect, useRef, useState } from 'react';
import mermaid from 'mermaid';

import { ThemeToggleContext } from '@/contexts/ThemeToggleContext.ts';

type MermaidElement = {
  id: string;
  code: string;
  element: Element;
  isLoading: boolean;
};

export const useMermaid = (html: string) => {
  const contentRef = useRef<HTMLDivElement>(null);
  const { theme } = useContext(ThemeToggleContext);
  const [mermaidElements, setMermaidElements] = useState<MermaidElement[]>([]);
  const [isInitialized, setIsInitialized] = useState(false);
  const initializedRef = useRef(false);

  // Mermaid   
  const getMermaidConfig = useCallback((currentTheme: string) => {
    const isDark = currentTheme === 'dark';
    return {
      startOnLoad: false,
      theme: (isDark ? 'dark' : 'default') as 'dark' | 'default',
      securityLevel: 'loose' as const,
      darkMode: isDark,
      themeVariables: isDark
        ? {
            primaryColor: '#1a1a1a',
            primaryTextColor: '#ffffff',
            primaryBorderColor: '#ffffff',
            lineColor: '#ffffff',
            secondaryColor: '#2d2d2d',
            tertiaryColor: '#3a3a3a',
            background: '#0f0f0f',
            mainBkg: '#1a1a1a',
            secondBkg: '#2d2d2d',
            tertiaryBkg: '#3a3a3a',
            secondaryTextColor: '#ffffff',
            tertiaryTextColor: '#ffffff',
            textColor: '#ffffff',
            loopTextColor: '#ffffff',
            noteTextColor: '#ffffff',
            activationTextColor: '#ffffff',
            signalTextColor: '#ffffff',
            labelTextColor: '#ffffff',
            actorTextColor: '#ffffff',
            messageLine0: '#ffffff',
            messageLine1: '#ffffff',
            messageText: '#ffffff',
            labelColor: '#ffffff',
            labelBackground: '#1a1a1a',
            cScale0: '#1a1a1a',
            cScale1: '#2d2d2d',
            cScale2: '#3a3a3a',
          }
        : {
            primaryColor: '#ffffff',
            primaryTextColor: '#000000',
            primaryBorderColor: '#000000',
            lineColor: '#000000',
            secondaryColor: '#f8f9fa',
            tertiaryColor: '#ffffff',
            background: '#ffffff',
            mainBkg: '#ffffff',
            secondBkg: '#f8f9fa',
            tertiaryBkg: '#ffffff',
            secondaryTextColor: '#000000',
            tertiaryTextColor: '#000000',
            textColor: '#000000',
            loopTextColor: '#000000',
            noteTextColor: '#000000',
            activationTextColor: '#000000',
            signalTextColor: '#000000',
            labelTextColor: '#000000',
            actorTextColor: '#000000',
            messageLine0: '#000000',
            messageLine1: '#000000',
            messageText: '#000000',
            labelColor: '#000000',
            labelBackground: '#ffffff',
            cScale0: '#ffffff',
            cScale1: '#f8f9fa',
            cScale2: '#e9ecef',
          },
    };
  }, []);

  //  HTML   Mermaid  
  const initializeMermaidElements = useCallback(() => {
    if (!contentRef.current || initializedRef.current) return;

    contentRef.current.innerHTML = html;
    const codeElements = contentRef.current.querySelectorAll('code.language-mermaid');

    if (codeElements.length === 0) {
      setIsInitialized(true);
      return;
    }

    const elements: MermaidElement[] = Array.from(codeElements).map((element, index) => ({
      id: `mermaid-${Date.now()}-${index}`,
      code: element.textContent || '',
      element,
      isLoading: true,
    }));

    setMermaidElements(elements);
    initializedRef.current = true;
  }, [html]);

  //     
  const addLoadingSpinner = useCallback((element: Element) => {
    if (!element.parentElement) return;

    const wrapper = document.createElement('div');
    wrapper.className = 'mermaid-loading-container';
    wrapper.style.cssText = `
      position: relative;
      min-height: 100px;
      margin: 1rem 0;
      padding: 2rem;
      background: var(--highlight-background);
      border: 1px dashed var(--border-top-color);
      border-radius: 8px;
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 12px;
      font-size: 14px;
      color: var(--body-color);
    `;

    const spinner = document.createElement('div');
    spinner.className = 'mermaid-spinner';
    spinner.style.cssText = `
      width: 20px;
      height: 20px;
      border: 2px solid var(--border-top-color);
      border-top: 2px solid var(--anchor-color);
      border-radius: 50%;
      animation: mermaid-spin 1s linear infinite;
    `;

    const text = document.createElement('span');
    text.textContent = '  ...';

    wrapper.appendChild(spinner);
    wrapper.appendChild(text);

    // CSS   ( )
    if (!document.querySelector('#mermaid-spinner-style')) {
      const style = document.createElement('style');
      style.id = 'mermaid-spinner-style';
      style.textContent = `
        @keyframes mermaid-spin {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }
      `;
      document.head.appendChild(style);
    }

    element.parentElement.replaceWith(wrapper);
    return wrapper;
  }, []);

  //    
  const renderMermaidDiagrams = useCallback(async () => {
    if (!contentRef.current || mermaidElements.length === 0) return;

    mermaid.initialize(getMermaidConfig(theme));

    try {
      //     (  )
      const renderPromises = mermaidElements.map(async ({ id, code, element }, index) => {
        let isCompleted = false;
        let loadingContainer: Element | null = null;

        // 300ms      
        const loadingTimer = setTimeout(() => {
          if (!isCompleted && element.parentElement) {
            loadingContainer = addLoadingSpinner(element) || null;
          }
        }, 300);

        try {
          const result = await mermaid.render(`${id}-${theme}`, code);
          isCompleted = true;
          clearTimeout(loadingTimer);

          //   
          const wrapper = document.createElement('div');
          wrapper.innerHTML = result.svg;
          wrapper.className = 'mermaid-diagram';
          wrapper.style.textAlign = 'center';
          wrapper.style.margin = '1rem 0';

          //    ,    
          const targetElement = loadingContainer || element.parentElement;
          if (targetElement) {
            targetElement.replaceWith(wrapper);
          }

          return { index, success: true };
        } catch (error) {
          isCompleted = true;
          clearTimeout(loadingTimer);
          console.error(`Mermaid rendering error for ${id}:`, error);

          //   
          const errorElement = document.createElement('div');
          errorElement.style.cssText = `
            color: var(--anchor-color);
            font-size: 14px;
            text-align: center;
            padding: 1rem;
            margin: 1rem 0;
            background: var(--highlight-background);
            border-radius: 8px;
          `;
          errorElement.innerHTML = '     .';

          const targetElement = loadingContainer || element.parentElement;
          if (targetElement) {
            targetElement.replaceWith(errorElement);
          }

          return { index, success: false };
        }
      });

      await Promise.all(renderPromises);
      setIsInitialized(true);
    } catch (error) {
      console.error('Mermaid batch rendering error:', error);
      setIsInitialized(true);
    }
  }, [mermaidElements, theme, getMermaidConfig, addLoadingSpinner]);

  //     
  const handleThemeChange = useCallback(async () => {
    if (!isInitialized || mermaidElements.length === 0) return;

    //     
    const diagrams = contentRef.current?.querySelectorAll('.mermaid-diagram');
    if (!diagrams) return;

    mermaid.initialize(getMermaidConfig(theme));

    try {
      const updatePromises = Array.from(diagrams).map(async (diagram, index) => {
        const mermaidEl = mermaidElements[index];
        if (!mermaidEl) return;

        let isCompleted = false;
        let hasSpinner = false;

        // 300ms      
        const loadingTimer = setTimeout(() => {
          if (!isCompleted) {
            hasSpinner = true;
            (diagram as HTMLElement).style.opacity = '0.5';
            (diagram as HTMLElement).style.filter = 'blur(1px)';
          }
        }, 300);

        try {
          const result = await mermaid.render(`${mermaidEl.id}-${theme}`, mermaidEl.code);
          isCompleted = true;
          clearTimeout(loadingTimer);

          //      SVG 
          if (hasSpinner) {
            (diagram as HTMLElement).style.opacity = '';
            (diagram as HTMLElement).style.filter = '';
          }
          (diagram as HTMLElement).innerHTML = result.svg;
        } catch (error) {
          isCompleted = true;
          clearTimeout(loadingTimer);
          console.error(`Mermaid theme update error for diagram ${index}:`, error);

          //     
          if (hasSpinner) {
            (diagram as HTMLElement).style.opacity = '';
            (diagram as HTMLElement).style.filter = '';
          }
        }
      });

      await Promise.all(updatePromises);
    } catch (error) {
      console.error('Mermaid theme change error:', error);
    }
  }, [isInitialized, mermaidElements, theme, getMermaidConfig]);

  // HTML  
  useEffect(() => {
    initializedRef.current = false;
    setIsInitialized(false);
    setMermaidElements([]);
    initializeMermaidElements();
  }, [html, initializeMermaidElements]);

  // Mermaid   
  useEffect(() => {
    if (mermaidElements.length > 0 && !isInitialized) {
      renderMermaidDiagrams();
    }
  }, [mermaidElements, renderMermaidDiagrams, isInitialized]);

  //     
  useEffect(() => {
    if (isInitialized) {
      handleThemeChange();
    }
  }, [theme, handleThemeChange, isInitialized]);

  return { contentRef };
};
